body {
	font-family: 'Noto Sans', sans-serif;
}

.header-v1 {
	background-color: #dedede;
}

.header-v1 .header-top {
	padding-top: 1rem;
	padding-right: 0;
	padding-bottom: 1rem;
	padding-left: 0;
}

